h3 {
    font-size: 15px;
    font-family: Helvetica, Arial, sans-serif;
    border-bottom: solid 5px #DEE5E5;
    text-align: center;
    color: #28464B;
    height: 20px;

}



.dayContainer {
    display: flex;
    flex-direction: column;
    border: solid 1px #DEE5E5;
    flex-grow: 3;
    position: relative;
    text-align: center;
    align-items: stretch;
    height: 716px;

}


.dragDayContainer {
    display: flex;
    flex-direction: column;
    border: solid 1px #DEE5E5;
    /* background-color: #DEE5E5; */
    flex-grow: 3;
    position: relative;
    text-align: center;
    align-items: stretch;
    height: 716px;

}

.timeBarContainer {
    display: flex;
    flex-direction: column;
    border: solid 1px #DEE5E5;
    flex-grow: 1;
    position: relative;
    text-align: center;    
    align-items: stretch;
    height: 716px;


}

.timeSlots {
    position: relative;
    z-index: 1;
    height: 630px;

}

.timeSlot {
    font-size: 15px;
    color: #28464B;
    font-family: Helvetica, Arial, sans-serif;
    border-top: solid 1px #F0F7EE;
    height: 20px;
    width: 10vw;
    /* height: 3vh; */

}

.timeSlotTop {
    /* background-color: #ffffff; */
    font-size: 15px;
    color: #28464B;
    font-family: Helvetica, Arial, sans-serif;
  
    border-top: solid 1px #DEE5E5;
    width: 10vw;

    height: 20px;
    /* height: 3vh; */

}
.slot {
    color: #ffffff;

}

