.eventButton {
    white-space: normal;
    word-wrap: break-word;

    color: #247BA0;
    background-color: #ffffff;
    position: absolute;
    border: none;
    border-left: 1px solid #247BA0;
    border-right: 10px solid #247BA0;
    border-top: 1px solid #247BA0;
    border-bottom: 1px solid #247BA0;

    width: 10vw;
    border-radius: 5px;
    
    z-index: 2;
    cursor: pointer;
    -webkit-appearance: none;
    overflow: scroll;


}

.eventButtonHover {
    white-space: normal;
    word-wrap: break-word;

    color: #ffffff;
    background-color: #247BA0;
    position: absolute;
    border-left: 1px solid #DEE5E5;
    border-right: 10px solid #DEE5E5;
    border-top: 1px solid #DEE5E5;
    border-bottom: 1px solid #DEE5E5;
    width: 10vw;
    border-radius: 5px;
    z-index: 2;
    cursor: pointer;
    -webkit-appearance: none;
    overflow: scroll;


}

@media screen and (max-width: 600px) {
    .eventButton {
        white-space: normal;
        word-wrap: break-word;
    
        color: #247BA0;
        background-color: #ffffff;
        position: absolute;
        border: none;
        border-left: 1px solid #247BA0;
        border-right: 10px solid #247BA0;
        border-top: 1px solid #247BA0;
        border-bottom: 1px solid #247BA0;
    
        width: 70vw;
        border-radius: 5px;
        
        z-index: 1;
        cursor: pointer;
        -webkit-appearance: none;
        overflow: scroll;
    
    
    }
    
    .eventButtonHover {
        white-space: normal;
        word-wrap: break-word;
    
        color: #ffffff;
        background-color: #247BA0;
        position: absolute;
        border-left: 1px solid #DEE5E5;
        border-right: 10px solid #DEE5E5;
        border-top: 1px solid #DEE5E5;
        border-bottom: 1px solid #DEE5E5;
        width: 70vw;
        border-radius: 5px;
        z-index: 1;
        cursor: pointer;
        -webkit-appearance: none;
        overflow: scroll;
    
    
    }

}